<template>
  <div class="userinfo">
    <div class="user-left" @click="viewInfo">
      <img :src="levelSrc" alt="" class="user-level" />
      <img class="head" :src="headSrc" />
      <!-- <img class="head" src="../assets/home/head@2x.png" alt="" /> -->
    </div>
    <div class="user-right">
      <div class="tbox user-name">{{ userInfo.nick_name }}</div>
      <div class="tbox user-star">
        <img src="../assets/home/star@2x.png" alt="" class="starimg" />
        <div class="starnum">{{ userInfo.available }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonModal',

  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      // userInfo: this.$store.getters.userInfo,
      // levelSrc: './static/img/user/level' + this.$store.getters.userInfo.leveal + '@2x.png',
      // headSrc: './static/img/user/head' + this.$store.getters.userInfo.role + '@2x.png'
      headSrc: './static/img/user/head' + JSON.parse(localStorage.getItem('userInfo')).data.icon_type + '@2x.png',
      levelSrc: './static/img/user/level' + JSON.parse(localStorage.getItem('userInfo')).data.leveal + '@2x.png'
    }
  },
  methods: {
    showModal () {
      this.visible = true
    },
    hideModal () {
      this.$emit('update:visible', false)
    },
    viewInfo () {
      this.$router.push('userset')
    }
  }
}
</script>

<style lang="less" scoped>
@import url("../assets/css/mixin.less");
.userinfo {
  width: 400px;
  height: 110px;
  padding: 22px 0 0 24px;
  .flex_func(row, flex-start, center, nowrap);
  .user-left {
    height: 96px;
    width: 96px;
    position: relative;
    // background: url(../assets/home/level_gold@2x.png);
    // .bg-image("../assets/home/level_gold","png");
    .flex_func(row, center, center, nowrap);
    .user-level {
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
    }
    .head {
      width: 88px;
      height: 88px;
      position: absolute;
      left: 4px;
      top: 4px;
    }
  }
  .user-right {
    width: 235px;
    height: 88px;
    // background: grey;
    margin-left: 18px;
    .tbox {
      width: 100%;
      height: 43px;
      line-height: 43px;
      font-size: 24px;
      color: #445680;
      background: rgba(255, 255, 255, 0.65);
      border-radius: 16px;
      padding: 0 16px;
    }
    .user-star {
      margin-top: 2px;
      .flex_func(row, space-between, center, nowrap);
      .starimg {
        width: 41px;
        height: 35px;
      }
    }
  }

  .head {
    width: 88px;
    height: 88px;
  }
}
</style>



